import React, { useEffect, useState } from "react";
import "./Navbar.css";
import NavLogo from "../../images/karlovdvorIcon.png";
import MenuIcon from "@material-ui/icons/Menu";
import CloseIcon from "@material-ui/icons/Close";

const Navbar = () => {
  const [show, setShow] = useState(false);
  const [logo, setLogo] = useState(true);
  const [toggleMenu, setToggleMenu] = useState(false);

  const menuHandler = () => {
    setToggleMenu((prev) => !prev);
  };

  const transitionNavbar = () => {
    if (window.scrollY > 100) {
      setShow(true);
    } else {
      setShow(false);
    }
  };

  const showLogo = () => {
    if (window.innerWidth <= 768) {
      setLogo(false);
    } else {
      setLogo(true);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", transitionNavbar);

    return () => window.removeEventListener("scroll", transitionNavbar);
  }, []);

  useEffect(() => {
    window.addEventListener("resize", showLogo);

    return () => window.removeEventListener("resize", showLogo);
  }, []);

  return (
    <div
      className={`navbar ${show && "navbar__active"} ${
        toggleMenu && "navbar_hamburger__active"
      }`}
    >
      {!toggleMenu ? (
        <MenuIcon className="menuIcon" onClick={menuHandler} />
      ) : (
        <CloseIcon className="menuIcon" onClick={menuHandler} />
      )}

      <nav
        className={`nav ${show && "nav__active"} ${
          toggleMenu && "nav_hamburger__active "
        }`}
      >
        <ul onClick={menuHandler}>
          <li>
            <a href="#jedlo">Reštaurácia</a>
          </li>
          <li>
            <a href="#program">Svadba</a>
          </li>

          {logo && (
            <li className="nav__logo">
              <img
                src={NavLogo}
                alt="karlov dvor"
                className={` ${show && "logo__show"}`}
              />
            </li>
          )}
          <li>
            <a href="#ponuka">O nás</a>
          </li>
          <li>
            <a href="#footer__wrapper">Kontakt</a>
          </li>
        </ul>
      </nav>
    </div>
  );
};

export default Navbar;
