import React from "react";
import "./Ponuka.css";
import preNevestu from "../../images/pre_nevestu.jpg";
import preZenicha from "../../images/pre_zenicha.jpeg";
import prostredie from "../../images/prostredie.jpeg";
import ubytovanie from "../../images/ubytovanie.jpeg";

const Ponuka = () => {
  return (
    <div className="ponuka" id="ponuka">
      <div
        className="ponuka__prenevestu"
        style={{ backgroundImage: `url(${preNevestu})` }}
      >
        <h2>PRE NEVESTU</h2>
        <p>
          Izba na svadobné prípravy Nekonečné možnosti fotografovania, tanečný
          parket, exkluzívne prostredie. V cene je zahrnuté privítanie v piatok
          a brunch v nedelu pre 8 ľudí
        </p>
      </div>
      <div
        className="ponuka__prezenicha"
        style={{ backgroundImage: `url(${preZenicha})` }}
      >
        <h2>PRE ŽENÍCHA</h2>
        <p>Kvalitné rakúske suroviny. Možnosť "korkovné”.</p>
      </div>
      <div
        className="ponuka__prostredie"
        style={{ backgroundImage: `url(${prostredie})` }}
      >
        <h2>PRE HOSTÍ</h2>
        <p>Veľké parkovisko. Svadobné menu od 99€.</p>
        <p>Minimálne 30 a maximálne 100 hostí</p>
      </div>
      <div
        className="ponuka__ubytovanie"
        style={{ backgroundImage: `url(${ubytovanie})` }}
      >
        <h2>PRE NAJBLIŽŠÍCH</h2>
        <p>
          Ubytovanie na 2 noci pre 8 hostí v cene v apartmánoch, ktoré patria k
          areálu.
        </p>
      </div>
    </div>
  );
};

export default Ponuka;
