import React from "react";
import "./Hero.css";
import HeroImg from "../../images/hero2.jpg";

const Hero = () => {
  return (
    <div className="hero">
      <img src={HeroImg} alt="karlov dvor" />
      <h1>Radi Vám pomôžeme s Vašou vysnívanou svadbou</h1>
      <h2>Voľné termíny aj na rok 2021</h2>
      <button>Rezervujte si svoj deň</button>
    </div>
  );
};

export default Hero;
