import React from "react";
import "./PlanPobytu.css";
import PlanPobytuCard from "./PlanPobytuCard";
import terasaImg from "../../images/terasa.jpeg";
import weddingImg from "../../images/weddingCrop.jpg";
import brunchImg from "../../images/brunch.jpeg";

const PlanPobytu = () => {
  return (
    <section className="planpobytu__wrapper" id="program">
      <h1>Program a plán pobytu</h1>
      <div className="plan__pobytu">
        <PlanPobytuCard
          image={terasaImg}
          day="Piatok"
          title1="PRIVÍTANIE"
          text1="Od 18:00"
          title2="UBYTOVANIE"
          text2="8 najblizsich ubytujeme v 3 apartmánoch"
        />
        <PlanPobytuCard
          image={weddingImg}
          day="Sobota"
          title1="SVADBA"
          text1="Od 12:00"
          title2="HOSTINA"
          text2="Slávnostnÿ obed s obsluhou od 19:00 bufet"
        />
        <PlanPobytuCard
          image={brunchImg}
          day="Nedeľa"
          title1="BRUNCH"
          text1="Od 10:00"
          title2="CHECKOUT"
          text2="Late checkout do 15:00 doplnkový program"
        />
      </div>
    </section>
  );
};

export default PlanPobytu;
