import React from "react";
import "./Jedlo.css";
import MasterChefs from "../../images/masterchefs.jpg";
import dezert from "../../images/menu/dezert.jpeg";
import polievka from "../../images/menu/polievka.jpeg";
import predjedlo from "../../images/menu/predjedlo.jpeg";
import hlavne_jedlo from "../../images/menu/hlavne_jedlo.jpeg";
import samoob1 from "../../images/menu/samoob1.jpeg";
import samoob2 from "../../images/menu/samoob2.jpeg";
import samoob3 from "../../images/menu/samoob3.jpeg";
import samoob4 from "../../images/menu/samoob4.jpeg";

const Jedlo = () => {
  return (
    <div className="jedlo-wrapper" id="jedlo">
      <div className="jedlo">
        <div className="svadobnemenu">
          <h1>SVADOBNÉ MENU</h1>
          <h3>od 12:00</h3>

          <div className="svadobnejedla">
            <div className="predjedlo">
              <img src={predjedlo} alt="predjedlo" />
            </div>
            <div className="polievka">
              <img src={polievka} alt="predjedlo" />
            </div>
            <div className="hlavnychod">
              <img src={hlavne_jedlo} alt="predjedlo" />
            </div>
            <div className="dezert">
              <img src={dezert} alt="predjedlo" />
            </div>
          </div>
          <div className="svadobnejedla__text">
            <p>predjedlo</p>
            <p>polievka</p>
            <p>hlavný chod</p>
            <p>dezert</p>
          </div>
        </div>

        <div className="svadobnemenu__bufet">
          <h1>SAMOOBSLUŽNÝ BUFET</h1>
          <h3>od 19:00</h3>

          <div className="svadobnejedla__bufet">
            <div className="predjedlo">
              <img src={samoob1} alt="" />
            </div>
            <div className="polievka">
              <img src={samoob2} alt="" />
            </div>
            <div className="hlavnychod">
              <img src={samoob3} alt="" />
            </div>
            <div className="dezert">
              <img src={samoob4} alt="" />
            </div>
          </div>
        </div>

        <div className="jedlo__footer">
          <div className="jedlo__img">
            <img src={MasterChefs} alt="nas kucharsky tim" />
          </div>
          <div className="jedlo__text">
            <h2>Náš kuchársky tím</h2>
            <h3>Marcel Ihnačák a Henrich Slezák</h3>
            <p>Dvaja ostrielaní kuchári a gurmáni.</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Jedlo;
