import React from "react";
import "./Footer.css";
import PhoneIcon from "@material-ui/icons/Phone";
import FacebookIcon from "@material-ui/icons/Facebook";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import LocationOnIcon from "@material-ui/icons/LocationOn";

const Footer = () => {
  return (
    <footer id="footer__wrapper">
      <div className="footer">
        <div className="contacts">
          <h2>Kontakt</h2>
          <p>
            <span>
              <FacebookIcon />
            </span>
            Karlov Dvor
          </p>
          <p>
            <span>
              <PhoneIcon />
            </span>
            Tel. 0905 616 084
          </p>

          <p>
            <span>
              <MailOutlineIcon />
            </span>
            karlovdvor@karlovdvor.sk
          </p>
          <p>
            <span>
              <LocationOnIcon />
            </span>
            M. R. Štefánika 18, 919 43 Cífer
          </p>
        </div>
        <div className="social_media">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d10608.757333766041!2d17.492256!3d48.337666!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xae90e3810fcf9d8b!2sKarlov%20Dvor%20Restaurant!5e0!3m2!1ssk!2ssk!4v1627999825681!5m2!1ssk!2ssk"
            title="map"
            loading="lazy"
            frameBorder="none"
          ></iframe>
        </div>
        <div className="greetings">
          <p>Tešíme sa na Vás.</p>
          <p>
            &copy; <b>Henrich Slezák</b>, Karlov Dvor
          </p>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
