import React from "react";
import "./PlanPobytuCard.css";

const PlanPobytuCard = ({ image, day, title1, text1, title2, text2 }) => {
  return (
    <div className="PPcard">
      <div className="planDay">
        <img src={image} alt={day} />
      </div>
      <div className="plan">
        <h2>{day}</h2>
        <div className="sectionOne">
          <h3>{title1}</h3>
          <p>{text1}</p>
        </div>
        <div className="sectionTwo">
          <h3>{title2}</h3>
          <p>{text2}</p>
        </div>
      </div>
    </div>
  );
};

export default PlanPobytuCard;
